/* Responsive Design */
@media (max-width: 768px) {
  .full-roulette {
    padding: 20px;
    margin: 10px;
  }
  
  .title {
    font-size: 2rem;
  }
  
  .roulette-wheel {
    width: 300px;
    height: 300px;
  }
  
  .snacks-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .roulette-wheel {
    width: 250px;
    height: 250px;
  }
  
  .title {
    font-size: 1.5rem;
  }
  
  .compact-roulette {
    padding: 15px;
  }
  
  .snacks-grid {
    grid-template-columns: 1fr;
  }
  
  .menu-popover {
    padding: 15px;
    max-height: 90vh;
    width: 95vw;
  }
  
  .menu-header h3 {
    font-size: 1.1rem;
  }
  
  .controls-row {
    flex-direction: column;
    gap: 8px;
  }
  
  .hamburger-button {
    min-width: 60px;
  }
  
  .result-popover {
    max-width: 95vw;
    width: 95vw;
  }
  
  .result-content {
    padding: 30px 20px;
  }
  
  .result-content h2 {
    font-size: 2rem;
  }
  
  .close-result-button {
    width: 35px;
    height: 35px;
    font-size: 16px;
  }
}
